<template>
  <v-container  fill-height fluid>
    <v-row>
      <v-col col="1" justify="center" align="left">
        <v-container class="px-0" fluid>
          <h1>Motor Control</h1>
        </v-container>
        <v-container class="px-0" fluid>
          <v-switch v-model="auto" :label="`Auto: ${auto}`"></v-switch>
        </v-container>
      </v-col>
      <v-col>
        <v-chip v-if="$store.state.awsiot.auto">
          Target Temp: {{ $store.state.awsiot.setPoint }}
        </v-chip>
        <temp-viewer :label="'Control temp : '" :unit="'C'" :scale="1" :value="$store.state.awsiot.tc" />
        <temp-viewer :label="'Meat 1 : '" :unit="'C'" :scale="1" :value="$store.state.awsiot.t1" />
        <temp-viewer :label="'Meat 2 : '" :unit="'C'" :scale="1" :value="$store.state.awsiot.t2" />
        <temp-viewer :label="'Meat 3 : '" :unit="'C'" :scale="1" :value="$store.state.awsiot.t3" />
      </v-col>
    </v-row>
  </v-container>
</template>



<script>
import { mapGetters } from "vuex";
import TempViewer from "../components/TempViewer.vue";

export default {
  components: { TempViewer },
  computed: {
    ...mapGetters(["tc", "t1", "t2", "t3", "fanSpeed", "farenheight"]),
    auto: {
      get() {
        if (this.$store.state.awsiot.auto === undefined) {
          this.updateShadow("bbq-micro", "auto", false);
        }
        return this.$store.state.awsiot.auto;
      },
      set(value) {
        this.updateShadow("bbq-micro", "auto", value);
      },
    },
  },
  data() {
    return {};
  },
  methods: {},
  mounted() {

  },
};
</script>