<template>
  <v-container fill-height fluid>
    <v-row class="justify-center">
      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          BBQ Dashboard
        </h1>
      </v-col>

      <v-col class="mb-5" cols="12">
        <h2 class="headline font-weight-bold mb-3">
          Current Meat Temps
        </h2>


      </v-col>

      <v-row justify="center">
        
        <v-col v-if="$store.state.awsiot.t1!='None'">
          Meat temp 1

          <v-sparkline :value="value" :gradient="gradient" :smooth="radius || false" :padding="padding"
            :line-width="lineWidth" :stroke-linecap="lineCap" :gradient-direction="gradientDirection" :fill="fill"
            :type="type" :auto-line-width="autoLineWidth" auto-draw :show-labels="showLabels" :label-size="labelSize">
          </v-sparkline> <!-- Just an example sparkline , remove example data and gradients if removed-->
        </v-col>
        <v-col v-if="$store.state.awsiot.t2!='None'">
          Meat Temp 2
          <v-sparkline :value="value" :gradient="gradient" :smooth="radius || false" :padding="padding"
            :line-width="lineWidth" :stroke-linecap="lineCap" :gradient-direction="gradientDirection" :fill="fill"
            :type="type" :auto-line-width="autoLineWidth" auto-draw :show-labels="showLabels" :label-size="labelSize">
          </v-sparkline> <!-- Just an example sparkline , remove example data and gradients if removed-->
        </v-col>
        <v-col v-if="$store.state.awsiot.t3!='None'">
          Meat Temperature 3
          <v-sparkline :value="value" :gradient="gradient" :smooth="radius || false" :padding="padding"
            :line-width="lineWidth" :stroke-linecap="lineCap" :gradient-direction="gradientDirection" :fill="fill"
            :type="type" :auto-line-width="autoLineWidth" auto-draw :show-labels="showLabels" :label-size="labelSize">
          </v-sparkline> <!-- Just an example sparkline , remove example data and gradients if removed-->
        </v-col>
      </v-row>
    </v-row>
  </v-container>
</template>

<script>
const gradients = [
  ['#222'],
  ['#42b3f4'],
  ['red', 'orange', 'yellow'],
  ['purple', 'violet'],
  ['#00c6ff', '#F0F', '#FF0'],
  ['#f72047', '#ffd200', '#1feaea'],
]
export default {
  name: 'HelloWorld',

  data: () => ({
    showLabels: false, //Sparkline Start
    lineWidth: 2,
    labelSize: 7,
    radius: 10,
    padding: 8,
    lineCap: 'round',
    gradient: gradients[5],
    value: [0, 2, 5, 9, 5, 10, 3, 5, -4, -10, 1, 8, 2, 9, 0],
    gradientDirection: 'top',
    gradients,
    fill: false,
    type: 'trend',
    autoLineWidth: false,  // Sparkline End
    ecosystem: [
      {
        text: 'vuetify-loader',
        href: 'https://github.com/vuetifyjs/vuetify-loader',
      },
      {
        text: 'github',
        href: 'https://github.com/vuetifyjs/vuetify',
      },
      {
        text: 'awesome-vuetify',
        href: 'https://github.com/vuetifyjs/awesome-vuetify',
      },
    ],
    importantLinks: [
      {
        text: 'Documentation',
        href: 'https://vuetifyjs.com',
      },
      {
        text: 'Chat',
        href: 'https://community.vuetifyjs.com',
      },
      {
        text: 'Made with Vuetify',
        href: 'https://madewithvuejs.com/vuetify',
      },
      {
        text: 'Twitter',
        href: 'https://twitter.com/vuetifyjs',
      },
      {
        text: 'Articles',
        href: 'https://medium.com/vuetify',
      },
    ],
  }),
}
</script>
