<template>
  <v-app id="inspire" background fill-height>
    <v-navigation-drawer color="primary" v-if="$auth.isAuthenticated" v-model="drawer" app>
      <v-list>
        <v-list-item v-for="item in items" :key="item.title" :to="item.to" link>
          <v-list-item-icon>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <!-- <template v-slot:preppend> -->
      <template>
        <div class="pa-2">
          <v-btn block v-if="$auth.isAuthenticated" class @click.prevent="logout"> Logout </v-btn>
        </div>
      </template>
    </v-navigation-drawer>

    <v-app-bar fluid color="primary" app min-width="100px">
      <v-app-bar-nav-icon v-if="$auth.isAuthenticated" @click="drawer = !drawer"></v-app-bar-nav-icon>

      <v-toolbar-title>Kamodo</v-toolbar-title>
      <v-spacer />
      <v-spacer></v-spacer>

      <v-icon v-if="$auth.isAuthenticated" left> {{ batteryMDI }} </v-icon>
      <v-icon v-if="$auth.isAuthenticated" left> {{ battery }} </v-icon>


      <v-btn icon @click="toggle_dark_mode">
        <v-icon>mdi-theme-light-dark</v-icon>
      </v-btn>


    </v-app-bar>
    <v-main>
      <!-- <v-container background fill-height fluid> -->
      <router-view />
      <!-- </v-container> -->
      <Footer />
    </v-main>
  </v-app>
</template>

<script>
import { mapGetters } from "vuex"
import Login from "./components/Login";
import Error from "./components/Error";
import Footer from "./components/Footer.vue";


export default {
  name: "App",
  components: {
    // NavBar,
    Login,
    Error,
    Footer,
  },
  computed: {
    ...mapGetters(["battery", "batteryMDI"])

  },
  methods: {
    /// Adds dark mode to web-app
    toggle_dark_mode: function () {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      localStorage.setItem("dark_theme", this.$vuetify.theme.dark.toString());
    },
    logout() {
      this.$auth.logout();
      this.$router.push({ path: "/" });
    },
  },
  watch: {
    $route: {
      immediate: true,
      handler(to) {
        document.title = to.meta.title || "Kamodo Control";
      },
    },
  },
  // computed: mapGetters(["temperature", "battery", "batteryMDI"]),
  data() {
    return {
      drawer: null,
      items: [
        { title: "Home", icon: "mdi-home", to: "/" },
        { title: "Control", icon: "mdi-view-dashboard", to: "/control" },
        { title: "Analytics", icon: "mdi-poll", to: "/analytics" },
      ],
    };
  },
  mounted() {
    const theme = localStorage.getItem("dark_theme");
    if (theme) {
      if (theme === "true") {
        this.$vuetify.theme.dark = true;
      } else {
        this.$vuetify.theme.dark = false;
      }
    } else if (
      window.matchMedia &&
      window.matchMedia("(prefers-color-scheme: dark)").matches
    ) {
      this.$vuetify.theme.dark = true;
      localStorage.setItem("dark_theme", this.$vuetify.theme.dark.toString());
    }
  },
};
</script>

