<template>
  <v-container fluid>
    <v-divider></v-divider>
    <br />
    <v-row>
      <v-col v-if="!this.$store.state.awsiot.auto">
        <v-subheader class="pl-0">Fan Speed: {{ this.$store.state.awsiot.fanSpeed }}%</v-subheader>
        <v-slider v-model="fanSpeed" thumb-label> <template v-slot:prepend>
            <v-icon @click="decrementFan">
              mdi-minus
            </v-icon>
          </template>
          <template v-slot:append>
            <v-icon @click="incrementFan">
              mdi-plus
            </v-icon>
          </template>
        </v-slider>
      </v-col>
      <v-col v-if="this.$store.state.awsiot.auto && this.$store.state.awsiot.farenheight">
        <v-subheader class="pl-0">Set Point: {{ this.$store.state.awsiot.setPoint }}&#8457;</v-subheader>
        <v-slider v-model="setPoint" thumb-label max="600">
          <template v-slot:prepend>
            <v-icon @click="decrement">
              mdi-minus
            </v-icon>
          </template>
          <template v-slot:append>
            <v-icon @click="increment">
              mdi-plus
            </v-icon>
          </template>
        </v-slider>
      </v-col>
      <v-col v-else-if="this.$store.state.awsiot.auto && !this.$store.state.awsiot.farenheight">
        <v-subheader class="pl-0">Set Point: {{ this.$store.state.awsiot.setPoint }}&#8451;</v-subheader>
        <v-slider v-model="setPoint" max="300" thumb-label>
          <template v-slot:prepend>
            <v-icon @click="decrement">
              mdi-minus
            </v-icon>
          </template>
          <template v-slot:append>
            <v-icon @click="increment">
              mdi-plus
            </v-icon>
          </template>
        </v-slider>
      </v-col>
    </v-row>

    <!-- <v-slider v-model="setPoint" :color="color" track-color="grey" always-dirty min="40" max="218">
      <template v-slot:prepend>
        <v-icon :color="color" @click="decrement">
          mdi-minus
        </v-icon>
      </template>

      <template v-slot:append>
        <v-icon :color="color" @click="increment">
          mdi-plus
        </v-icon>
      </template>
    </v-slider> -->
    <v-row>
      <v-col>
        <v-card>

          <v-card-text>
            <v-switch colour='primary' v-model="auto" label="Auto Mode"></v-switch>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col>
        <v-card>

          <v-card-text>
            <v-switch colour='primary' v-model="farenheight" label="Farenheight"></v-switch>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>



<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["tc", "t1", "t2", "t3", "fanSpeed", "farenheight", "setPoint"]),
    auto: {
      get() {
        if (this.$store.state.awsiot.auto === undefined) {
          this.updateShadow("bbq-micro", "auto", false);
        }
        return this.$store.state.awsiot.auto;
      },
      set(value) {
        this.updateShadow("bbq-micro", "auto", value);
      },
    },
    setPoint: {
      get() {
        if (this.$store.state.awsiot.setPoint === undefined) {
          this.updateShadow("bbq-micro", "setPoint", false);
        } else if(this.$store.state.awsiot.setPoint > 300) {
          this.updateShadow("bbq-micro", "setPoint", 300);
        }
        return this.$store.state.awsiot.setPoint;
      },
      set(value) {
        this.updateShadow("bbq-micro", "setPoint", value);
      },
    },

    fanSpeed: {
      get() {
        if (this.$store.state.awsiot.fanSpeed === undefined) {
          this.updateShadow("bbq-micro", "fanSpeed", false);
        }
        return this.$store.state.awsiot.fanSpeed;
      },
      set(value) {
        this.updateShadow("bbq-micro", "fanSpeed", value);
      },
    },
    farenheight: {
      get() {
        if (this.$store.state.awsiot.farenheight === undefined) {
          this.updateShadow("bbq-micro", "farenheight", false);
        }
        return this.$store.state.awsiot.farenheight;
      },
      set(value) {
        this.updateShadow("bbq-micro", "farenheight", value);
      },
    },

    data() {
      return {};
    },

  },
  methods: {
    decrement() {
      this.$store.state.awsiot.setPoint--
    },
    increment() {
      this.$store.state.awsiot.setPoint++
    },
    decrementFan() {
      this.$store.state.awsiot.fanSpeed--
    },
    incrementFan() {
      this.$store.state.awsiot.fanSpeed++
    },
  },
};
</script>