import store from "../store";
var AWS = require('aws-sdk');
var AWSIoTData = require('aws-iot-device-sdk');
var connectionState = false;
var thingName = 'bbq-micro';
var shadows = {};
//var shadows; 

var AWSConfiguration = {
    poolId: 'ap-southeast-2:0a48a54b-dd24-4d42-b3c6-64ffc9e8cf11', // 'YourCognitoIdentityPoolId'
    host: 'a2309kzg9gfpvv-ats.iot.ap-southeast-2.amazonaws.com', // 'YourAWSIoTEndpoint', e.g. 'prefix.iot.us-east-1.amazonaws.com'
    region: 'ap-southeast-2' // 'YourAwsRegion', e.g. 'us-east-1',

};

AWS.config.region = AWSConfiguration.region;

AWS.config.credentials = new AWS.CognitoIdentityCredentials({
    IdentityPoolId: AWSConfiguration.poolId
});

var cognitoIdentity = new AWS.CognitoIdentity();
function getAWSIdentity() {
    return new Promise((resolve, reject) => {
        AWS.config.credentials.get(function (err) {
            if (!err) {
                console.log('retrieved identity: ' + AWS.config.credentials);
                var params = {
                    IdentityId: AWS.config.credentials.identityId
                };
                cognitoIdentity.getCredentialsForIdentity(params, function (err, data) {
                    if (!err) {
                        resolve(data);
                    } else {
                        console.log('error retrieving credentials: ' + err);
                        reject(err);
                    }
                });
            } else {
                console.log('error retrieving identity:' + err);
                reject(err);
            }
        });
    })
}

getAWSIdentity().then((identity) => {


    shadows = AWSIoTData.thingShadow({
        region: AWS.config.region,
        host: AWSConfiguration.host,
        clientId: (Math.floor((Math.random() * 100000) + 1)),
        protocol: 'wss',
        maximumReconnectTimeMs: 8000,
        debug: true,
        accessKeyId: identity.Credentials.AccessKeyId,
        secretKey: identity.Credentials.SecretKey,
        sessionToken: identity.Credentials.SessionToken
    });

    shadows.register(thingName);

    shadows.on('connect', () => {
        console.log('connected to AWS IoT');
        connectionState = true;
    });

    shadows.on('close', () => {
        console.log('close');
        shadows.unregister(thingName);
        connectionState = false;
    });

    shadows.on('message', (topic, payload) => {
        console.log('message', topic, payload.toString());
    });

    shadows.on('reconnect', () => {
        console.log('reconnect');
        connectionState = true;
    });

    shadows.on('delta', (name, stateObject) => {
        console.log('delta', name, stateObject);
        store.dispatch('updateAWS', stateObject.state);
    });

    shadows.on('status', (name, type, clientToken, shadowState) => {
        console.log('status', name, type, shadowState);
        if(shadowState.reported){
        console.log(shadowState.reported);
        }
       // store.dispatch('updateAWS', shadowState.state.delta);
    
    });

    shadows.on('timeout', (thingName, clientToken) => {
        console.log('timeout', thingName, clientToken);
    });

    setTimeout(() => {
        //connection must be fully established before the state snapshot is requested
        console.log(shadows.get(thingName));
        //this get will return under the 'status' listener
    }, 3000);


});

function updateShadow(thingName, path, payload) {

    console.log(path);
    console.log(payload)
    if (connectionState) {
        console.log("Publishing data to AWS: ", thingName, path ,payload);
        var newState = `{ "state": { "desired":{"${path}": ${payload}}}}`
        console.log(newState)
        shadows.update(thingName, JSON.parse(newState));
    }
}


const updatePluggin = {
    // eslint-disable-next-line no-unused-vars
    install(Vue, options) {
        Vue.mixin({
            // mounted() {
            //     console.log('Mounted!');
            // },
            methods: {
                updateShadow: function (thingName, path, payload) {
                    console.log('Shadow Updated! ' + payload)
                    updateShadow(thingName, path, payload)
                   

                }
            }
        });
    }
};

export default updatePluggin;