<template>
    <v-btn color = "primary" 
      v-if="!$auth.isAuthenticated && !$auth.loading"
      @click.prevent="login"
    >
      Login
    </v-btn>
</template>

<script>
export default {
  name: "Login",
  methods: {
    login() {
      this.$auth.loginWithRedirect();
    },
    logout() {
      console.log("returning to ", window.location.origin)
      //this.$auth.logout({ returnTo: window.location.origin });
      this.$auth.logout({ returnTo: "https://grillhouse.github.io/bbq-webapp/" });
      //this.$router.push({ path: "/" });
    },
  },
};
</script>

